import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Image, Button, Modal, Form, Container, Row, Col } from 'react-bootstrap';

class PublicComment extends Component {

  constructor(props) {
	super(props);
    this.state = {
      comment: this.props.comment,
      score: this.props.score 
    };
  }
  
  componentDidUpdate(prevProps){
    if (this.props.comment !== prevProps.comment){
      this.setState({
        comment: this.props.comment
      });
    }
    if (this.props.score !== prevProps.score){
      this.setState({
        score: this.props.score
      });
    }
  }

  render(){
    return(
      <Row className='pub-comment-bar'>
        <Col sm={3} className='pub-comment-score righteous'>{this.state.score}</Col>
        <Col className='pub-comment-text-container'><p className='pub-comment-text overpass'>{this.state.comment}</p></Col>
      </Row>
    );
  }
}

export default PublicComment;