//Profile public

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import jwt_decode from 'jwt-decode';
import NavigationBar from './Navbar.jsx';
import { Image, Button, Modal, Form, Container, Row, Col } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
import PublicComment from './PublicComment.jsx';
import '../css/profile.css';
import profTestData from '../test_data_prof.json';
import img404 from '../404_placeholder.png';

class ProfilePublic extends Component {

  constructor(props) {
	super(props);
    //console.log(this.props.match.params.id);
    
    this.state = {
      profName: 'Placeholder',
      totalSocialScore: 0,
      summary: '',
      scores: [],
      imgPlaceholder: img404,
      imgSrc: (new Blob()),
      imgSet: false
    }
  }

  componentDidMount(){
    //let tok = this.props.location.state.token;
    
    console.log(this.state);
    
    if(sessionStorage.getItem("sstoken") === null){
      this.props.history.push({
          pathname: '/'
      });
    }else{
      let tok = sessionStorage.getItem("sstoken");
      //let id = jwt_decode(tok).sub
      let headstart = {
        Authorization: `Bearer ${tok}`
      };
    
      fetch(('http://localhost:8080/v1/user/' + this.props.match.params.id), {
        method: 'GET',
        headers: headstart
      })
      .then(response => response.json())
      .then( (result) =>{
        console.log(result);
        console.log(this.state);
        
        if(result._id !== undefined){
           let namer = '';
          if (!result.activated){
            namer = 'unknown user';
          }else{
            namer = result.first_name + ' ' + result.last_name;
          }
          this.setState({
            profName: namer,
            totalSocialScore: result.social_score,
            scores: result.scores,
            summary: result.caption
          });
          console.log(this.state.scores);
          if(result.photo !== null){
            fetch(('http://localhost:8080/v1/file/' + result.photo),{
              method: 'GET',
              headers: headstart
            }).then(res => res.blob())
            .then((result) => {
              console.log(result);
              this.setState({
                imgSrc: result,
                imgSet: true
              });
            })
            .catch((err) =>{
              console.log(err);
            });
          }
        }else{
         this.props.history.push({
          pathname: '/profile/'
         });
        }
        
      })
    }
    
  }
  
   render() {
    return(
      <div>
        <NavigationBar mview={jwt_decode(sessionStorage.getItem("sstoken")).mod}/>
        <Container className='public-profile-main'>

          {/*TOP SECTION */}
          <Row className='top-profile-section'>
            <Col sm={3}><Image id='profile-img' src={(this.state.imgSet ? (URL.createObjectURL(this.state.imgSrc)) : this.state.imgPlaceholder)}/></Col>
            <Col>
              <h1 className='kanit' id='person-name'>{ this.state.profName }</h1>
              {/*<h2 className='kanit' id='person-location'>{profTestData.profile_location}</h2>*/}
            </Col>
            <Col sm={2}>
              <h1 className='righteous' id='social-score'>{ this.state.totalSocialScore }</h1>
            </Col>
          </Row>

          {/*SUMMARY SECTION */}
          <Row className='profile-section'>
            <h2 className='kanit section-header'>About {this.state.profName}</h2>
            <p className='overpass summary-text'>{this.state.summary}</p>
          </Row>

          {/*COMMENT SECTION */}
          <section>
            <h2 className='kanit section-header'>Recent Comments</h2>
            { this.state.scores.map( (item) => {
                 return(<PublicComment score={item.score} comment={item.caption}/>);
              })
            }
            {/*<Button bsPrefix='gray-button' className='kanit'>See more</Button>*/}
          </section>


        </Container>
      </div>
    );
  }

}

export default withRouter(ProfilePublic);
