//Profile private

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import jwt_decode from 'jwt-decode';
import NavigationBar from './Navbar.jsx';
import { Image, Button, Modal, Form, Container, Row, Col } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
import CommentCard from './CommentCard.jsx';
import CountdownTimer from './CountdownTimer.jsx';
import '../css/profile.css';
import pencilIcon from '../pencil_edit.svg';
import profTestData from '../test_data_prof.json';

class Profile extends Component {

  constructor(props) {
	super(props);
    
    let sum = '';
    let name = '';
    let locale = '';
    let scores = [];
    let socscore = -0.12;
    let nextsub = "2022-04-27 23:59:59";
    let idtemp = '8394';
    console.log(props);
    
    if(sessionStorage.getItem("sstoken") === null){
      this.props.history.push({
          pathname: '/'
      });
    }
    
    
    this.state = {
      visShare: false,
      summary: profTestData.profile_summary,
      tempText: profTestData.profile_summary,
      personName: profTestData.profile_name,
      location: profTestData.profile_location,
      lastLogin: profTestData.last_login,
      nextSub: nextsub,
      all_scores: scores,
      socialscore: socscore,
      id: idtemp,
      summEdit: false,
      submittable: false,
      m: false,
      imgSrc: (new Blob()),
      imgSet: false
    };

    this.showShare = this.showShare.bind(this);
    this.hideShare = this.hideShare.bind(this);
    this.changeTempText = this.changeTempText.bind(this);
    this.changeSummary = this.changeSummary.bind(this);
    this.toUpload = this.toUpload.bind(this);
    this.makeDispute = this.makeDispute.bind(this);
    //this.tester = this.tester.bind(this);
  }

  showShare = () => {
    this.setState({
      visShare: true
    });
  };

  toUpload = () => {
    if(this.state.submittable){
      this.props.history.push({
          pathname: '/upload'
      });
    }
  };

  hideShare = () => {
    this.setState({
      visShare: false
    });
  };

  changeTempText = (event) => {
    this.setState({
      tempText: event.target.value
    });
  };

  changeSummary = () => {
    let tok = sessionStorage.getItem("sstoken");
    let id = jwt_decode(tok).sub;
    let headstart = {
      Authorization: `Bearer ${tok}`
    };
    
    let form = new FormData();

    form.append("caption", this.state.tempText);
    
    fetch(('http://localhost:8080/v1/user/' + id), {
      method: 'PUT',
      headers: headstart,
      body: form
    })
    .then(response => response.json())
    .then( (result) =>{
      console.log(result);
      this.setState({
        summary: this.state.tempText,
        summEdit: false
      });
    });
    
    
  };

  makeDispute = (data, scoreid) => {
    console.log(data);
    
    let formdes = new FormData();
    
    formdes.append("disputed_caption",data);
    let headdes = {
      Authorization: `Bearer ${sessionStorage.getItem("sstoken")}`
    };
    
    fetch(('http://localhost:8080/v1/dispute_score/' + scoreid), {
      method: 'PUT',
      headers: headdes,
      body: formdes
    })
    .then(response => response.json())
    .then( (result) =>{
      console.log(result);
    });
    
  };

  openSummary = () => {
    this.setState({
      summEdit: true
    });
  };

  /*
  tester = () => {
    console.log(profTestData.profile_comments);
  };
  */

  componentDidMount() {
    
    if(sessionStorage.getItem("sstoken") === null){
      this.props.history.push({
          pathname: '/'
      });
    }else{
      console.log(sessionStorage.getItem("sstoken"));
      console.log("checker, token state");
      console.log(sessionStorage.getItem("sstoken"))
      let tok = sessionStorage.getItem("sstoken");
      let parseJwt = jwt_decode(tok);
      let id = parseJwt.sub;
      console.log(parseJwt);
      console.log(id);
      
      let headstart = {
        Authorization: `Bearer ${tok}`
      };
      
      fetch(('http://localhost:8080/v1/user/' + id), {
        method: 'GET',
        headers: headstart
      })
      .then(response => response.json())
      .then( (result) => {
        console.log("user below");
        console.log(result);
        let temptime = new Date(result.next_submission);
        temptime.setHours(temptime.getHours() - 4);
        console.log(temptime);
        this.setState({
          id: result._id,
          personName: result.first_name + ' ' + result.last_name,
          summary: result.caption,
          tempText: result.caption,
          nextSub: result.next_submission,
          socialscore: result.social_score,
          m: result.moderator,
          imgSrc: result.id_photo
        });
        const currtime = new Date();
        const diff = temptime.getTime() - currtime.getTime();
        if (diff > 0) {
          console.log("diff > 0");
          this.setState({
            submittable: false
          });
          console.log(this.state.submittable);
        }else{
          console.log("diff < 0");
          this.setState({
            submittable: true
          });
        }
        
        fetch(('http://localhost:8080/v1/file/' + result.id_photo),{
          method: 'GET',
          headers: headstart
        }).then(res => res.blob())
        .then((rest) => {
          console.log(rest);
          this.setState({
            imgSrc: rest,
            imgSet: true
          });
          //console.log(this.state.imgSrc);
        })
        .catch(err => {
          console.log(err);
        });
        
      })
      .catch(error => {
          console.log(error);
      });
      
      fetch(('http://localhost:8080/v1/scores/' + id), {
        method: 'GET',
        headers: headstart
      })
      .then(response => response.json())
      .then((result) => {
        console.log("scores below");
        console.log(result);
        this.setState({
          all_scores: result
        });
      })
      .catch(error => {
        console.log(error);
      });
      
      
      
    }

  }

   render() {
    return(
      <div>
        <NavigationBar mview={this.state.m}/>
        
        <Container>
          <Modal show={this.state.visShare} onHide={this.hideShare} className='share-modal'>
            <Modal.Header closeButton>
              <h2 className='kanit'>Share Your Score</h2>
            </Modal.Header>
            <Modal.Body className='overpass'>
              {'socialscorer.nickmeyer.net/profile/' + this.state.id}
            </Modal.Body>
          </Modal>

          {/* TOP SECTION */}
          <Row className='top-profile-section'>
            {this.state.imgSet ? <Col sm={3}><Image id='profile-img' src={URL.createObjectURL(this.state.imgSrc)}/></Col> : <Col sm={3}><Image id='profile-img' src={profTestData.profile_photo}/></Col>}
            <Col>
              <h1 className='kanit' id='person-name'>{this.state.personName}</h1>
              <h2 className='kanit' id='person-location'>{this.state.location}</h2>
            </Col>
            <Col sm={2}>
              <h1 className='righteous' id='social-score'>{ this.state.socialscore }</h1>
              <Button onClick={this.showShare} bsPrefix='gray-button' className='kanit'>Share</Button>
            </Col>
          </Row>

          {/* Summary section */}
          <section className='profile-section'>
            <h2 className='kanit section-header'>About You</h2>
            <div>
              <section className={'summary-filled ' + (!this.state.summEdit ? '' : 'hidden')}>
                <p className='overpass summary-text'>{this.state.summary}</p>
                <Button bsPrefix='gray-button' onClick={this.openSummary} id='edit-summary-button'><Image src={pencilIcon}/></Button>
              </section>
              <Form className={'summary-filled ' + (this.state.summEdit ? '' : 'hidden')}>
                <Form.Control as='textarea' row={3} value={this.state.tempText} onChange={this.changeTempText} className='overpass'/>
                <Button onClick={this.changeSummary} bsPrefix='gray-button' className='kanit' id='submit-summary-button'>Submit</Button>
              </Form>
            </div>
          </section>
          {/* Comment section */}
          <section className='profile-section'>
            <h2 className='kanit section-header'>Recent Comments</h2>

            <Row className='card-container'>
              {this.state.all_scores.map( (item) => {
                 return(<CommentCard id={item._id} photo={item.photo} score={item.score} summ={item.caption} date={item.timestamp} logDate={this.state.timestamp} submitDispute={this.makeDispute}/>);
              })}
              {
                (this.state.all_scores.length > 0) ? <div></div> :  <p className="overpass left-align">No comments</p>
              }
              {/*<Button onClick={this.tester}>:D</Button>*/}
            </Row>
            {/*<Button bsPrefix='gray-button' className='kanit'>See more</Button>*/}
          </section>
          {/* Submit new score */}
          <Row className='profile-section'>
            <h2 className='kanit section-header'>Submit New Score</h2>
            <Col><Button className='gray-button kanit gray-link-button' id='prof-submit-button' disabled={!this.state.submittable} onClick={this.toUpload}>Submit score</Button></Col>

            <Col className='kanit timer-section' as={Row}>
              <Col sm={6} className='timer-label'>Time remaining:</Col>
              <Col sm={6} className='timer-countdown'>{(this.state.socialscore !== -0.12) ? <CountdownTimer nextsub = {this.state.nextSub}/> : <div></div>}</Col>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }

}

export default withRouter(Profile);
