//upload score

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import jwt_decode from 'jwt-decode';
import { Button, Modal, Form, Container, Image, Row, Col } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
import '../css/upload.css';
import NavigationBar from './Navbar.jsx';
import facepic from '../face_screenshot.png';

class UploadPage extends Component {

  constructor(props) {
	super(props);
    this.state = {
      scoreVal: 0,
      showMultiface: false,
      showFoundface: false,
      showUpImg: false,
      disableSubmit: true,
      commentText: '',
      m: false,
      imgUploaded: true,
      textAdded: true,
      imgRes: [],
      offsetx: -1,
      offsety: -1
    };
    
    this.orgImg = React.createRef();
    this.changeScoreVal = this.changeScoreVal.bind(this);
    this.changeComment = this.changeComment.bind(this);
    this.findFace = this.findFace.bind(this);
    this.checkImg = this.checkImg.bind(this);
    this.getValues = this.getValues.bind(this);
    this.fullSubmission = this.fullSubmission.bind(this);
  }

  changeScoreVal = (event) => {
    this.setState({
      scoreVal: event.target.value,
    });
  };

  changeComment = (event) => {
    this.setState({
      commentText: event.target.value
    });
    if (this.state.commentText.trim() !== '') {
      this.setState({
        textAdded: true
      });
    }
  };

  getValues = (v1,v2) => () => {
    event.preventDefault();
    this.setState({
      offsetx: v1,
      offsety: v2,
      disableSubmit: false
    });
  };

  fullSubmission = (event) => {
    event.preventDefault();
    let formsub = new FormData();
    
    formsub.append("caption", this.state.commentText);
    formsub.append("score_photo",this.orgImg.current.files[0]);
    const date = (new Date(this.orgImg.current.files[0].lastModified)).toISOString();
    //console.log(date);
    formsub.append("timestamp",date);
    formsub.append("score",this.state.scoreVal);
    formsub.append("face_offset_x",parseInt(this.state.offsetx));
    formsub.append("face_offset_y",parseInt(this.state.offsety));
    //form.append
    
    for (var key of formsub.entries()) {
        console.log("/n");
        console.log(key[0]);
        console.log(key[1]);
      }

    let headerstime = {
      Authorization: `Bearer ${sessionStorage.getItem("sstoken")}`
    };

    fetch('http://localhost:8080/v1/score',{
      method: 'POST',
      headers: headerstime,
      body: formsub
    })
    .then((response) => response.json())
    .then((result) =>{
      console.log(result);
      this.props.history.push({
          pathname: '/me'
      });
    });
  }
  

  findFace = () => {
    //console.log(this.orgImg.current.files[0]);
    //const date = new Date(this.orgImg.current.files[0].lastModified)
    //console.log(`${this.orgImg.current.files[0].name} has a last modified date of ${date}`)
    
    //MAKE THE FORM
    let con = true;
    if(this.orgImg.current.files[0] === undefined){
      this.setState({
        imgUploaded: false
      });
      con = false
    }
    if(this.state.commentText === ''){
      this.setState({
        textAdded: false
      });
      con = false
    }
    if(this.state.scoreVal === 0){
       con = false
    }
    if(con){
      let formdata = new FormData();
    
      formdata.append("caption", this.state.commentText);
      formdata.append("score_photo",this.orgImg.current.files[0]);
      const date = (new Date(this.orgImg.current.files[0].lastModified)).toISOString();
      //console.log(date);
      formdata.append("timestamp",date);
      formdata.append("score",this.state.scoreVal);
      //form.append
      for (var key of formdata.entries()) {
        console.log();
        console.log(key[0]);
        console.log(key[1]);
      }

      let headerstime = {
        Authorization: `Bearer ${sessionStorage.getItem("sstoken")}`
      };
      
      fetch('http://localhost:8080/v1/score',{
        method: 'POST',
        headers: headerstime,
        body: formdata
      })
      .then((response) => response.json())
      .then((result) =>{
        console.log(result);
        this.setState({
          imgRes: result
        });
        console.log(this.state.imgRes);
      }); //make this into the right endpoint

      this.setState({
        showFoundface: true,
        showMultiface: true,
        showUpImg: true
      });
    }
    
  };

  checkImg = () => {
    if (!this.state.imgUploaded){
      this.setState({
        imgUploaded: true
      });
    }
  }


  componentDidMount(){
    if(sessionStorage.getItem("sstoken") === null){
      this.props.history.push({
          pathname: '/'
      });
    }else{
      let tok = sessionStorage.getItem("sstoken");
      let id = jwt_decode(tok).sub;
      console.log(id);
      let headstart = {
        Authorization: `Bearer ${tok}`
      };
      fetch(('http://localhost:8080/v1/user/' + id), {
        method: 'GET',
        headers: headstart
      }).then(response => response.json())
      .then( (result) => {
        console.log(result);
        this.setState({
          m: result.moderator
        });
      })
    }
  }

   render() {
    return(
      <div>
        <NavigationBar mview={this.state.m} />
        <Container>
          <h1 className='righteous'>Score Upload</h1>

          <Form id="fullSubmit" onSubmit={this.fullSubmission}>

            <Form.Group className='upload-form-section'>
              {/*Upload image*/}
              <h2 className='kanit'>Upload image</h2>
              <Form.Control className={'overpass ' + (this.state.imgUploaded ? '' : 'is-invalid')} type='file' accept='image/*' ref={this.orgImg} onChange={this.checkImg}/>
              { this.state.imgUploaded ? <div></div> : <Form.Text className='form-text-cond'> Please upload an image </Form.Text>}
              {/*<Image id='uploaded-img' className={this.state.showUpImg ? '' : 'hidden'} src='https://previews.123rf.com/images/piksel/piksel1306/piksel130600124/20469966-business-people-cheering-at-laptop.jpg'/>*/}
            </Form.Group>

            {/* This bar is hidden for now, it needs work still */}
            <div>
              <Form.Group className={this.state.showMultiface ? '' : 'hidden'}>
                {/*Bar for identifying faces, make it like a flexbox or something*/}
                <p className='overpass'>Face(s) found. Please indicate the person you want to score.</p>
                <Row className="">
                  {
                    this.state.imgRes.map( (item) => {
                      //this is the nastiest thing i have ever laid eyes upon
                      return(<Col sm={3} className="multiface-container" style={{overflow: "hidden"}}><button style={{maxWidth: (item[2][0] - item[0][0]), maxHeight: (item[2][1] - item[0][1]), overflow: "hidden"}} onClick={this.getValues( (item[2][0] + item[0][0])/2, (item[2][1] + item[0][1])/2)}><Image className='multiface-photo' src={URL.createObjectURL(this.orgImg.current.files[0])} style={{margin: ( -(item[0][1])+ "px 0px 0px " + -(item[0][0]) + "px" )}} /></button></Col>)
                    })
                  }
                </Row>
                {/*<Image className='multiface-photo' src={facepic}/>
                <Image src='#'/>*/}
              </Form.Group>
            </div>

            {/*<Form.Group className={'barfound ' + (this.state.showFoundface ? '' : 'hidden')}>
              {/*Section for identifying person
              <h2 className='kanit'>People Found</h2>
              <Row className='person-found-bar'>
                {this.state.imgRes.map}
              </Row>
              <Button bsPrefix='gray-button' className='kanit'>Search Again</Button>
            </Form.Group>*/}

            <Form.Group className='upload-form-section'>
              {/*Slider*/}
              <h2 className='kanit'>Your Score</h2>
              <Row className='justify-content-center'>
                <Col sm={6}><Form.Range min='-10' max='10' onChange={this.changeScoreVal} className={'form-slider '  + ((this.state.scoreVal !== 0) ? '' : 'is-invalid')}/></Col>
                <Col sm={2}><Form.Label className='righteous slider-label'>{this.state.scoreVal}</Form.Label></Col>
              </Row>
              {(this.state.scoreVal !== 0) ? <div></div> : <Form.Text className='form-text-cond'> Please add a score </Form.Text>}
            </Form.Group>

            <Form.Group className='upload-form-section'>
              {/*Comment*/}
              <h2 className='kanit'>Your Comment</h2>
              <Form.Control className={'overpass '  + (this.state.textAdded ? '' : 'is-invalid')} as='textarea' rows={3} placeholder='type comment here' value={this.state.commentText} onChange={this.changeComment}/>
              { this.state.textAdded ? <div></div> : <Form.Text className='form-text-cond'> Please add a comment </Form.Text>}
            </Form.Group>
          </Form>
          
          <div>
            <Button bsPrefix='gray-button' onClick={this.findFace} className='kanit'>Click to scan</Button>
          </div>

          <div className='button-panel-upload'>
            <Button bsPrefix='gray-button' className='righteous gray-link-button' as={Link} to={'/me'}>Cancel</Button>
            <Button type="submit" form="fullSubmit" bsPrefix='gray-button' className='righteous' disabled={this.state.disableSubmit}>Submit</Button>
          </div>

        </Container>
      </div>
    );
  }

}

export default withRouter(UploadPage);
