//navbar

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Navbar, NavDropdown, Image, Button } from 'react-bootstrap';
import { Link, withRouter } from 'react-router-dom';
import sslogo from '../socialscorerTM.svg';

class NavigationBar extends Component {

  constructor(props) {
	super(props);
    this.state = {
      temp: '',
      mview: this.props.mview
    };

    this.handleLogout = this.handleLogout.bind(this);
  }

  handleLogout = () => {
    sessionStorage.removeItem("sstoken");
    this.props.history.push({
          pathname: '/'
    });
  };

  componentDidMount(){
    if (sessionStorage.getItem("sstoken") !== null){
      //console.log('nav mview: ' + this.state.mview)
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.mview !== this.props.mview) {
      this.setState({
        mview: this.props.mview
      });
    }
  }

  render() {
    return(
      <Navbar className='nav-topbar'>
        <div className='navbar-img-title'>
          <Image src={sslogo} id='navbar-logo'/>
          <Navbar.Brand className='righteous' id='navbar-title' as={Link} to={'/me'}>Social Scorer</Navbar.Brand>
        </div>
        <div className="navbar-button-panel">
          {this.state.mview ? <Link role="button" className='black-button black-button-navbar righteous' to='/moddash'>Overseer</Link> : <div></div>}
          <Button className="black-button black-button-navbar righteous" onClick={this.handleLogout}>Logout</Button>
        </div>
      </Navbar>
    );
  }

}

export default withRouter(NavigationBar);
