// countdown timer

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Link, withRouter } from 'react-router-dom';


class CountdownTimer extends Component {


  constructor(props) {
	super(props);
    this.state = {
      ogDate: this.props.nextsub,
      timesup: false,
      hours: '00',
      min: '00',
      sec: '00',
      intID: {}
    };
  }

  componentDidMount() {
   
   let countdownint = setInterval(() => {
      const currDate = new Date();
      let origDate = new Date(this.state.ogDate);
      origDate.setHours(origDate.getHours() - 4);
      const difference = origDate - currDate;
      if (difference < 1) {
         this.setState({ timesup: true });
      } else {
         const days = Math.floor(difference / (1000 * 60 * 60 * 24));
         let hours = Math.floor((difference / (1000 * 60 * 60)) % 24);
         const minutes = Math.floor((difference / (1000 * 60)) % 60);
         const seconds = Math.floor((difference / (1000)) % 60);
        hours += days * 24;
         this.setState({
            hours: hours > 9 ? hours : `0${hours}`,
            min: minutes > 9 ? minutes : `0${minutes}`,
            sec: seconds > 9 ? seconds : `0${seconds}`,
         });
      }
   }, 1000);
    
  this.setState({
    intID: countdownint
  });
}

  componentWillUnmount() {
    clearInterval(this.state.intID);
  }

  render() {
    return (
      <p>{this.state.timesup ? '00:00:00' : `${this.state.hours} : ${this.state.min} : ${this.state.sec} ` }</p>
    );
  }
}

export default withRouter(CountdownTimer);
