// dispute cards

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import {Button, Modal, Card, Image, Col, Row, Form} from 'react-bootstrap';

class DisputeCard extends Component{

  constructor(props) {
	super(props);
    this.state = {
      modalVisible: false,
      nameSearch: '',
      origComment: this.props.origComment,
      rebuttalComment: this.props.rebuttal,
      locationstring: '123 location street',
      score: this.props.score,
      img: this.props.imgLink,
      scoreVal: this.props.score,
      scoreAdjust: false,
      imgBlob: (new Blob())
    };
  
    this.showModal = this.showModal.bind(this);
    this.hideModal = this.hideModal.bind(this);
    this.changeScoreVal = this.changeScoreVal.bind(this);
    this.submitScoreAdjust = this.submitScoreAdjust.bind(this);
    this.approveRebuttal = this.approveRebuttal.bind(this);
    this.denyRebuttal = this.denyRebuttal.bind(this);
  }

  showModal = () => {
    this.setState({
      modalVisible: true
    });
  };

  hideModal = () => {
    this.setState({
      modalVisible: false
    });
  };

  changeScoreVal = (event) => {
    this.setState({
      scoreVal: event.target.value
    });
  };


  approveRebuttal = () => {
    this.props.approveRebuttal(this.props.id);
  };

  denyRebuttal = () => {
    this.props.denyRebuttal(this.props.id,this.state.origComment);
  };

  openScoreAdjust = () => {
    this.setState({
      scoreAdjust: true
    });
  };

  submitScoreAdjust = () => {
    this.props.submitScoreAdjust(this.props.id, this.state.scoreVal);
//    this.setState({
//      scoreAdjust: false,
//      score: this.state.scoreVal
//    });
    
  };

  componentDidMount(){
    let headstart = {
        Authorization: `Bearer ${sessionStorage.getItem("sstoken")}`
      };
    
    console.log(this.props);
    
    fetch(('http://localhost:8080/v1/file/' + this.state.img),{
        method: 'GET',
        headers: headstart
      }).then(res => res.blob())
      .then((rest) => {
        console.log(rest);
        this.setState({
          imgBlob: rest
        });
        //console.log(this.state.imgSrc);
      })
      .catch(err => {
        console.log(err);
      });
  }

   render() {
    return(
      <Col sm={3}>
        <Modal show={this.state.modalVisible} onHide={this.hideModal}>
          <Image src={URL.createObjectURL(this.state.imgBlob)} className='card-img'/>
          <p className='overpass dispute-card-piclocation'>{this.state.locationstring}</p>
          <Modal.Body>
            <Row className='dispute-score-and-text'>
              <Col sm={3}><div className='righteous dispute-modal-score'>{this.state.score}</div></Col>
              <Col>
                <h2 className='kanit'>Original comment</h2>
                <p className='overpass'>{this.state.origComment}</p>
                <h2 className='kanit'>Rebuttal</h2>
                <p className='overpass'>{this.state.rebuttalComment}</p>
              </Col>
            </Row>  
            <div className='dispute-card-button-panel'> {/*button panel*/}
              <Button bsPrefix='gray-button' className='kanit' onClick={this.denyRebuttal}>Deny Rebuttal</Button>
              <Button bsPrefix='gray-button' className='kanit' onClick={this.openScoreAdjust}>Adjust Score</Button>
              <Button bsPrefix='gray-button' className='kanit' onClick={this.approveRebuttal}>Approve Rebuttal</Button>
            </div>
            <div className={(this.state.scoreAdjust ? '' : 'hidden')}>
              <Row className='align-items-center dispute-slider-section'>
                <Col><Form.Range min='-10' max='10' onChange={this.changeScoreVal} className='dispute-adjust-slider'/></Col>
                <Col sm={2} className='adjust-slider-label'><Form.Label className='righteous'>{this.state.scoreVal}</Form.Label></Col>
              </Row>
              <Button bsPrefix='gray-button' className='kanit' onClick={this.submitScoreAdjust}>Submit</Button>
            </div>
          </Modal.Body>
        </Modal>
        <Card onClick={this.showModal} className='moddash-card'>
          <Card.Img variant='top' src={URL.createObjectURL(this.state.imgBlob)} />
          <Card.Body>
            <Card.Title className='dispute-card-score righteous'>{this.state.score}</Card.Title>
          </Card.Body>
        </Card>
      </Col>
    );
  }

}

export default DisputeCard;
