//mod cards

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Button, Modal, Card, Image, Col } from 'react-bootstrap';

class ModeratorCard extends Component {

  constructor(props) {
	super(props);
    this.state = {
      id: this.props.id,
      currentMod: this.props.mod,
      name: this.props.fullName,
      imgSrc: this.props.imgLink,
      imgBlob: (new Blob())
      //modalVisible: false,
      //nameSearch: ""
    }; 

    this.approval = this.approval.bind(this);
    this.denial = this.denial.bind(this);
  }

  
  approval = () => {
    this.props.approval(this.props.id)
  };
  
  denial = () => {
    this.props.denial(this.props.id)
  };
  
/*
  hideModal = () => {
    this.setState({
      modalVisible: false
    });
  }
  */

  componentDidMount() {
    console.log(this.state.name + ' ' + this.state.imgSrc);
    
    let headstart = {
        Authorization: `Bearer ${sessionStorage.getItem("sstoken")}`
      };
    
    fetch(('http://localhost:8080/v1/file/' + this.state.imgSrc),{
        method: 'GET',
        headers: headstart
      }).then(res => res.blob())
      .then((rest) => {
        console.log(rest);
        this.setState({
          imgBlob: rest
        });
        //console.log(this.state.imgSrc);
      })
      .catch(err => {
        console.log(err);
      });
  }

   render() {
    return(
      <Col sm={4}>
        <Card className='moddash-card'>
          <Card.Img variant='top' src={URL.createObjectURL(this.state.imgBlob)} className='card-img' />
          <Card.Body>
            <Card.Title className='mod-card-name' className='kanit'>{this.state.name}</Card.Title>
          </Card.Body>
          <Card.Footer className='moddash-modcard-footer'>
            <Button bsPrefix='gray-button' className={'kanit ' + (this.state.currentMod ? '' : 'hidden')} onClick={this.denial}>Remove</Button>
            <Button bsPrefix='gray-button' className={'kanit ' + (!this.state.currentMod ? '' : 'hidden')} onClick={this.approval}>Add</Button>
          </Card.Footer>
        </Card>
      </Col>
    );
  }

}

export default ModeratorCard;
